@import "https://fonts.googleapis.com/css?family=Poppins:200,300,400,500,600,700,800,900&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

body {
  min-height: 100vh;
  justify-content: center;
  align-items: center;
  display: flex;
  overflow: hidden;
}

ul {
  position: relative;
}

ul li {
  text-align: center;
  list-style: none;
}

ul li a {
  color: #333;
  border-radius: 30px;
  padding: 5px 20px;
  font-size: 3em;
  font-weight: 700;
  text-decoration: none;
  transition: all .5s;
  display: inline-flex;
}

ul:hover li a {
  color: #0002;
}

ul li:hover a {
  color: #000;
  background: #fff;
  border-radius: 30px;
}

ul li a:before {
  content: "";
  color: #0000001a;
  z-index: -1;
  opacity: 0;
  text-transform: uppercase;
  letter-spacing: 500px;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-size: 4em;
  font-weight: 900;
  transition: letter-spacing 1s, left .5s;
  display: flex;
  position: absolute;
  top: 50%;
  left: 40%;
  transform: translate(-50%, -50%);
}

ul li a:hover:before {
  content: attr(data-text);
  opacity: 1;
  letter-spacing: 10px;
  width: 1800px;
  height: 1800px;
  left: 50%;
}

ul li:nth-child(6n+1) a:before {
  background: #81ecec;
}

ul li:nth-child(6n+2) a:before {
  background: #ff7675;
}

ul li:nth-child(6n+3) a:before {
  background: #55efc4;
}

ul li:nth-child(6n+4) a:before {
  background: #a29bfe;
}

ul li:nth-child(6n+5) a:before {
  background: #fd79a8;
}

ul li:nth-child(6n+6) a:before {
  background: #ffeaa7;
}

